.form_control {
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}

.form_control label {
  margin-bottom: 0.6em;
  font-weight: bold;
}

.form_control input {
  padding: 0.7em;
  border-radius: 0;
  border: 1px solid transparent;
  transition: 0.3s;
}

.form_control input::placeholder {
  color: #7b7b7b;
}

.form_control input:focus {
  outline: none;
  border: 1px solid #ffbb33;
}
